import React, { useState, useRef, useEffect } from 'react';

function PhotoCapture({ onComplete }) {
  const videoRef = useRef(null);
  const canvasRef = useRef(null);
  const frameImageRef = useRef(null);
  const [photoCount, setPhotoCount] = useState(0);
  const [photos, setPhotos] = useState([]);
  const maxPhotos = 6;

  useEffect(() => {
    async function setupCamera() {
      try {
        console.log("   ...");
        const stream = await navigator.mediaDevices.getUserMedia({ video: true });
        console.log("  :", stream);
        if (videoRef.current) {
          videoRef.current.srcObject = stream;
        }
      } catch (error) {
        console.error("  :", error);
        alert("  : " + error.message);
      }
    }
    setupCamera();
  }, []);

  const capturePhoto = () => {
    const video = videoRef.current;
    const canvas = canvasRef.current;
    const context = canvas.getContext('2d');

    //    
    if (!video || video.videoWidth === 0 || video.videoHeight === 0) {
      console.warn("   .");
      alert("   .    .");
      return;
    }
    
    //   
    canvas.width = video.videoWidth;
    canvas.height = video.videoHeight;

    //    
    context.drawImage(video, 0, 0, canvas.width, canvas.height);

    //    
    if (!frameImageRef.current.complete) {
      console.warn("    .");
      frameImageRef.current.onload = () => processPhoto(canvas, context);
    } else {
      processPhoto(canvas, context);
    }
  };

  //       dataURL 
  const processPhoto = (canvas, context) => {
    //   
    context.drawImage(frameImageRef.current, 0, 0, canvas.width, canvas.height);
    
    //    ("Hidden Data" )
    let imageData = context.getImageData(0, 0, canvas.width, canvas.height);
    imageData = embedDataInImage(imageData, "Hidden Data");
    context.putImageData(imageData, 0, 0);

    const dataURL = canvas.toDataURL('image/png');
    console.log("  :", dataURL);
    
    setPhotos(prev => {
      const newPhotos = [...prev, dataURL];
      console.log(`  : ${newPhotos.length}/${maxPhotos}`);
      if (newPhotos.length === maxPhotos) {
        console.log("6   .   .");
        onComplete(newPhotos);
      }
      return newPhotos;
    });
    setPhotoCount(prev => prev + 1);
  };

  //        LSB  ( )
  const embedDataInImage = (imageData, data) => {
    const binaryString = toBinaryString(data);
    const pixels = imageData.data;
    let dataIndex = 0;
    for (let i = 0; i < pixels.length; i += 4) {
      if (dataIndex < binaryString.length) {
        pixels[i] = (pixels[i] & 0xFE) | parseInt(binaryString[dataIndex]);
        dataIndex++;
      } else {
        break;
      }
    }
    return imageData;
  };

  const toBinaryString = (text) => {
    return text
      .split('')
      .map((char) => {
        const binaryChar = char.charCodeAt(0).toString(2);
        return binaryChar.padStart(8, '0');
      })
      .join('');
  };

  return (
    <div className="photo-capture">
      <div className="camera-container">
        <video
          ref={videoRef}
          autoPlay
          playsInline
          onLoadedMetadata={() => {
            console.log("   :", videoRef.current.videoWidth, videoRef.current.videoHeight);
          }}
          style={{ width: '100%', maxWidth: '600px' }}
        />
      </div>
      <button onClick={capturePhoto} disabled={photoCount >= maxPhotos}>
          ({photoCount}/{maxPhotos})
      </button>
      {/*    */}
      <canvas ref={canvasRef} style={{ display: 'none' }} />
      {/*   (public/frame.png) */}
      <img
        ref={frameImageRef}
        src="/frame.png"
        alt="Frame Overlay"
        onLoad={() => console.log("   ")}
        style={{ display: 'none' }}
      />
      <div className="thumbnail-container">
        {photos.map((photo, index) => (
          <img
            key={index}
            src={photo}
            alt={`Thumbnail ${index + 1}`}
            style={{ width: '100px', margin: '5px' }}
          />
        ))}
      </div>
    </div>
  );
}

export default PhotoCapture;
